import { useRef, useEffect } from "react";

import * as styles from "./EnterCodeInput.module.css";

export default function EnterCodeInput(props) {
  const { digits, changeHandler } = props;
  const length = digits.length;
  //     input-
  const inputRefs = useRef([]);

  //       
  useEffect(() => {
    inputRefs.current[0].focus();
  }, []);

  const handleKeyDown = (index, event) => {
    if (event.key === "Backspace") {
      event.preventDefault();
      if (digits[index].match(/^[0-9]$/)) {
        //    digits  ,    
        //  callback- ,    
        const newDigits = [...digits]; //  digits
        newDigits[index] = "";
        changeHandler(newDigits);
      } else {
        //   digits ,       
        //     input   ,     
        if (index > 0) inputRefs.current[index - 1].focus();
      }
    }
  };

  const handleChange = (index, newValue) => {
    const oldDigit = digits[index];
    //      ,   
    const newDigit = newValue.trim().replace(oldDigit, "");
    //    ,   ,    
    if (newDigit < "0" || newDigit > "9") return;
    //   callback ,   digits
    const newDigits = [...digits]; //  digits
    newDigits[index] = newDigit;
    changeHandler(newDigits);
    //         
    if (index < length - 1) {
      inputRefs.current[index + 1].focus();
    } else {
      //   ,     
      inputRefs.current[index].blur();
    }
  };

  return (
    <div className={styles.codeForm}>
      {digits.map((digit, index) => (
        <input
          className={styles.codeItem}
          key={index}
          value={digit}
          onChange={(event) => handleChange(index, event.target.value)}
          onKeyDown={(event) => handleKeyDown(index, event)}
          ref={(element) => (inputRefs.current[index] = element)}
        />
      ))}
    </div>
  );
}

